
import tabGroup from '@/components/group/tab'
import {getMyDealList} from '@/api/deal'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
tabGroup: tabGroup,
Toast
},
data () {
return {
title: '我的账户',
a: true,
myDealList:[],
mymoney:0
}
},
props: {},
computed: {},
mounted () {
this.getMyDealList()
},
filters:{
formatDealType(dealType){
if(dealType ==1){
return '充值';
}else if(dealType ==2){
return '提现';
}else if(dealType ==3){
return '支付';
}else if(dealType ==4){
return '奖励';
}
}
},
methods: {
recharge () {
this.$vux.toast.show({
text:'敬请期待',
type:'text'
})

},
withdraw () {
this.$router.push('./withdraw')
},
getMyDealList () {
let data={
userId:this.$store.getters.userInfo.userId,
pageNum: 1,
pageSize: 10
}
getMyDealList(data).then(response=>{
console.log(response.data)
this.myDealList = response.data;
});

}


}
}



